import React, {  useState,useEffect } from "react";
import styles from "./Contact.module.css";
import emailjs from "@emailjs/browser";
import { Box, Button, Hide, Image, Input, Show, SimpleGrid, Text, Textarea } from "@chakra-ui/react";
import bg from "./image/email.png"


const Contact = () => {

  const [values, setValues] = useState({
    fullName: '',
    email: '',
  
    message: ''
  });
  const [status, setStatus] = useState('');
 
 
  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.send('service_x2yw01r', 'template_vi9ptcf', values, 'rtDiYcwa6UEkH1sk0')
      .then(response => {
        console.log('SUCCESS!', response);
        setValues({
          fullName: '',
          email: '',
         
          message: ''
        });
        setStatus('SUCCESS');
      }, error => {
        console.log('FAILED...', error);
      });
  }
 
  useEffect(() => {
    if(status === 'SUCCESS') {
      setTimeout(() => {
        setStatus('');
      }, 3000);
    }
  }, [status]);

  const handleChange = (e) => {
    setValues(values => ({
      ...values,
      [e.target.name]: e.target.value
    }))
  }
  return (
    <SimpleGrid columns={[1,1,1,2]}  gap={4} className={styles.contact_form} id="contact">
      {/* left side copy and paste from work section */}
        <Hide below="lg" >
        <Box >
            <Image src={bg}  alt="bg" />

        </Box>
        </Hide>
     
      {/* right side form */}
      <Box  width="85%" m="auto" >
      {status && renderAlert()}
        <form  onSubmit={handleSubmit} >
          
           
                    <Text fontSize={"xl"} fontWeight="600">Contact Me </Text>
                <br/>
                <br/>
                
           

          <Input value={values.fullName} classname={styles.user}     onChange={handleChange} label="Full Name" name="fullName" type="text" placeholder="Employer or Company name"/>
          <br/>
          <br/>
             <Input value={values.email} classname={styles.user}     onChange={handleChange} label="E-Mail" name="email" type="email" placeholder="abc@email.com"/>
            <br/>
            <br/>
          <Textarea value={values.message}  classname={styles.user}    onChange={handleChange} label="Your message here" name="message" placeholder="Your message please"/>
            <br/>
            <br/>
          <Button type="submit" value="Send" className={styles.button}>Send Email</Button>
         
          <Box
            className="blur c-blur1"
            style={{ background: "var(--purple)" }}
          ></Box>
        </form>
      </Box>
    </SimpleGrid>
  );
};
const renderAlert = () => (
  <div className="px-4 py-3 leading-normal text-blue-700 bg-blue-100 rounded mb-5 text-center">
    <p>Your message send successfully</p>
  </div>
)
export default Contact;












