import {
  Box,
  Button,
  Flex,
  Hide,
  Image,
  Show,
  SimpleGrid,
  StylesProvider,
  Text,
} from "@chakra-ui/react";
import React from "react";
import PROFILE from "./image/PROFILE.jpg";
import Styles from "./about.module.css";
import Typical from "react-typical";
import Resume from "./image/Amit_Singh_Chouhan_resume.pdf";
import { FaLaptopCode } from "react-icons/fa";
import Wave from "./image/Wave.gif";
import { FaDownload } from 'react-icons/fa'




const About = () => {
  return (
    <>
      <SimpleGrid
        name="about"
        columns={[1, 1, 2]}
        w="95%"
        margin="auto"
        justifyContent="space-between"
        marginTop={"120px"}
        boxShadow="lg"
        p="6"
        rounded="md"
        bg="white"
        backgroundImage={["none", "none", Wave]}
        backgroundSize="65%"
      >
        {/* <SimpleGrid name='about'  columns={[1, 1, 2]} w="95%" margin="auto"  justifyContent="space-between" marginTop={"120px"} boxShadow='lg' p='6' rounded='md' bg='white' backgroundSize="65%">     */}
        <Show below="md">
          <Box padding={"1em"} width="70%" m={"auto"}>
            <Image
              borderRadius={"50%"}
              src={PROFILE}
              width="400px"
              m={"auto"}
            />
          </Box>
        </Show>

        <Box
          height={"200px,auto,auto,auto"}
          width={["200-300", "auto", "auto", "600px"]}
          padding={"1em"}
          className={Styles.texts}
        >
          <Text
            color="#536174"
            fontWeight="700"
            fontSize={["x-large", "x-large", "x-lg", "xx-large"]}
          >
            Welcome to my Portfolio
          </Text>
          <Text
           
            fontWeight="700"
            color="#536174"
            fontSize={["x-large", "x-large", "x-lg", "xx-large"]}
          >
            Hi, I'm Amit Singh Chouhan
          </Text>
          {/* <Hide below='md'> */}
          <Text
          height={["50px","auto","auto","auto"]}
            color={"orange"}
            display="flex"
            fontWeight="700"
            fontSize={["lg", "lg", "x-lg", "xx-large"]}
          >
            <Box paddingTop={"10px"} marginRight="12px">
              <FaLaptopCode color={"teal"} />
            </Box>
            <Typical
              loop={Infinity}
              steps={[
                "A Full Stack Web Developer",
                2500,
                "MERN Stack Developer",
                2500,
              ]}
            />
          </Text>
          {/* </Hide> */}
          <a href={Resume} download>
            {" "}
            <Button position={"revert-layer"} mt={"2rem"} w={"120px"}>
              <Flex width="120px" _hover={{color:"red"}} justifyContent={"space-between"} ><FaDownload /> Resume </Flex>
            </Button>
          </a>
        </Box>

        <Hide below="md">
          <Box padding={"1em"} width="70%" m={"auto"}>
            <Image
              borderRadius={"50%"}
              src={PROFILE}
              width="400px"
              m={"auto"}
            />
          </Box>
        </Hide>
      </SimpleGrid>
    </>
  );
};

export default About;
